@charset 'utf-8';
@import 'base/global.scss';

/* 
 * system.scss 
 * 页眉, 菜单栏, 窗口/标签管理器
*/

/* 变量 */
// 页眉高度
$header-height: 60px;
// tabs选项卡高度
$tabs-height: 3.2em;
// 选项卡切换箭头宽度
$tabs-arrow-width: 30px;

/* 页眉 */
.sys-header {
  @extend %fix;
  // position: relative;
  width: 100%;
  height: $header-height;
  line-height: $header-height;
  color: #fff;

  a,
  a:hover {
    color: #fff;
  }

  .logo {
    height: 100%;
    padding: 5px;
    
    img {
      height: 100%;
    }
  }

  .links {
    display: block;
    height: 100%;
    margin: 0 20px;
    li {
      float: left;
      font-size: 0.8em;
      margin-right: 10px;
    }
  }

  @media only screen and (max-width: 1280px) {
   .links {
     display: none;
   } 
  }


  .control-panel {
    position: relative;
    height: 100%;
    margin: 0 20px;
  }

  .user-name {
    height: 100%;
    max-width: 500px;
  }


  .faArrow {
    font-size: 1.5em;
    vertical-align: middle;
  }
  .faArrow-down {
    display: inline-block;
  }
  .faArrow-up {
    display: none;
  }

  // 下拉菜单
  .control-panel .menus {
    position: absolute;
    visibility:  hidden;
    opacity: 0;
    right: 0;
    top: $header-height;
    padding: 5px 0;
    transition: 0.3s;
    z-index: 99;
    pointer-events: none;

    a {
      display: block;
      font-size: 12px;
      padding: 0 2em;
      line-height: 3.2em;
      word-break: keep-all;
      cursor: pointer;
    }

    .arrow-up {
      display: block;
      position: absolute;
      top: -12px;
      width: 100%;
      height: 20px;
      background: url($images-path + 'arrow-blue-up.png') no-repeat 10% center;
    }


  }

  .control-panel.active {

    .faArrow-up {
      display: inline-block;
      vertical-align: text-top;
    }
    .faArrow-down {
      display: none;
    }
    .menus {
      opacity: 1;
      top: $header-height + 11px;
      visibility: visible;
      pointer-events: auto;
    }
  }






}


/* 主体 */
.sys-body {
  &.outer {
    padding-top: $header-height;
  }

  .content-window {
    position: relative;
    overflow: hidden;
    padding-top: $tabs-height;
  }

  .windows-out {
    position: relative;
    overflow: hidden;
    height: 100%;
  }

  .windows-in {
    position: relative;
    height: 100%;
  }

  .tabs-out {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: $tabs-height;

    &.show-arrow {
      padding: 0 $tabs-arrow-width;
      .tabs-arrow {
        display: block;
      }

    }

  }


}

.sys-window {
  max-width: 100%;
}

/* 菜单栏 */
.menu-bar {
  width: 200px;
  transition: 0.3s;
}
// 普通菜单栏, 抽屉菜单栏通用样式
.navigation,
.drawer-nav {

  // 菜单项高度
  $menu-height: 4em;
  width: 100%;
  height: 100%;
  color: $Theme-text-third;
  background-color: #3b444f;
  overflow-y: auto;
  transition: none;

  a {
    @extend %ellipsis;
    position: relative;
    display: block;
    padding-left: 2em; // 36px
    padding-right: 1em; // 14.4px
    line-height: $menu-height;
    color: #d8d8d8;
    &:hover {
      background: #343e4b;
    }
  }
  .open .selected {
    color: $Theme-color;
    background: #343e4b;
    border-left: 2px solid $Theme-color;
    &:after {
      content:'';
      display: block;
      position: absolute;
      right: 0;
      top: $menu-height / 2 - 0.2em;
      width: 0;
      height: 0;
      border-bottom: 0.5em solid transparent;
      border-top: 0.5em solid transparent;
      border-right: 0.7em solid whitesmoke;
      line-height: 0;
    }
  }
  .open .selected.no-folder {
    border-left: none;
    color: $Theme-text-third;
  }

  .folder {
    @extend %ellipsis;
    position: relative;
    border-bottom: 1px solid #49515c;
    line-height: $menu-height;
    padding: 0 0 0 0.5em;
    cursor: pointer;
  }

  .folder:before {
    content: "\f107";
    display: inline-block;
    font: normal normal normal 14px/1 FontAwesome;
    font-size: inherit;
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
    margin-right: 0.8em;
  }

  .open .folder:before {
    content: "\f106";
  }

  li > ul {
    display: none;
    background-color: #2c3645;
  }
  li.open > ul {
    display: block;
  }
  .no-folder {
    border-bottom: 1px solid #49515c;
    color: $Theme-text-third;
  }
}
// 抽屉菜单栏
.drawer-wrap {
  // display: none;
  width: 200px;
  .open .selected {
    &:after {
      border-right: 0.7em solid #7c848e;
    }
  }
}
// 移动端隐藏普通菜单栏
@media screen and (max-width: 1280px) {
  .menu-bar {
    overflow: hidden;
    width: 0;
  }
}

/* 选项卡 */
// todo 尺寸等问题等先跑起来后在看着改
.tabs-arrow {
  position: absolute;
  display: none;
  top: 0;
  width: $tabs-arrow-width;
  // 减去的是字体大小, 达到垂直居中
  line-height: $tabs-height - 1.5em;
  font-size: 1.5em;
  text-align: center;
  height: 100%;
  background-color: $Theme-gray-light;
  color: $Theme-text-third;
  cursor: pointer;
  z-index: 2;

  &:hover {
    color: $Theme-text-primary;
    background-color: $Theme-gray-middle;
  }

  &.left {
    left: 0;
  }
  &.right {
    right: 0;
  }
}


.tabs-in {
  font-family: Tahoma, Verdana, STHeiTi, simsun, sans-serif;
  height: 100%;
  background: $Theme-gray-light;
  overflow: hidden;

  .tabs-pill {
    position: relative;
    width: 9999px;
    height: 100%;
  }
  .tabs {
    height: 100%;
    position: absolute;

    li {
      height: 100%;
      float: left;
      cursor: pointer;
    }

    li.first {
      a.inner .close,
      a.inner:hover .close {
        display: none;
        opacity: 0;
        filter: progid:DXImageTransform.Microsoft.Alpha(opacity=0);
      }
    }

    a.inner .title {
      color: $Theme-text-primary;
    }

    a.inner {
      @extend %ellipsis;
      display: block;
      position: relative;
      margin: 0;
      border: none;
      width: 140px;
      height: 100%;
      padding: 0 0.8em;
      line-height: $tabs-height;
      text-align: center;
      text-transform: uppercase;
      color: rgba(0,0,0,.54);
      overflow: hidden;
      background: linear-gradient(to bottom, #e0ecff 0, #e0ecff 100%);
      margin: 0 1px;
    }


    li.selected.first {
      a.inner .close {
        display: none;
        opacity: 0;
        filter: progid:DXImageTransform.Microsoft.Alpha(opacity=0);
      }
      
    }


    li.selected {
      a.inner {
        border-bottom: 3px solid #4c8dee;
        background: #eee;
        .close {
          display: block;
          opacity: 1;
          filter: progid:DXImageTransform.Microsoft.Alpha(opacity=100);
        }
      }
    }


    // todo 一下hover可能会引起iphone点击多次的BUG



    a.inner:hover {
      background: #d5e1f5;
      .title {
        color: #000;
      }
      .close {
        font-size: inherit;
        display: block;
        opacity: 1;
        filter: progid:DXImageTransform.Microsoft.Alpha(opacity=100);
      }
    }

    .close {
      position: absolute;
      display: block;
      top: 0;
      right: 5px;
      opacity: 0.6;
      width: 14px;
      height: 100%;
      filter: progid:DXImageTransform.Microsoft.Alpha(opacity=60);
      background: url($images-path + 'tabs_icons.png') -34px center no-repeat;
      i {
        vertical-align: baseline;
      }
    }


  }



}


/* iframe窗口内容 */
.iframes {
  position: relative;
  height: 100%;

  .iframe-box.active {
    // left: 0;
    pointer-events: auto;
    opacity: 1;
    filter: progid:DXImageTransform.Microsoft.Alpha(opacity=100);
    z-index: 5;
    visibility: visible;
  }

  .iframe-box {
    @extend %full-size;
    // left: -100%;
    pointer-events: none;
    opacity: 0;
    -webkit-overflow-scrolling: touch; 
    overflow: auto;
    filter: progid:DXImageTransform.Microsoft.Alpha(opacity=0);
    transition: 0.3s;
    visibility: hidden;
  }
  
  iframe {
    display: block;
    @extend %full-size;
  }

}



/* 移动端 */
@media screen and (max-width: 700px) {
  $header-height: 54px;
  .sys-header {
    height: $header-height;
    line-height: $header-height;

    .control-panel .menus {
      top: $header-height;
    }
    .control-panel.active .menus {
      top: $header-height + 11px;
    }
  }

  .sys-body {
    &.outer {
      padding-top: $header-height;
    }
  }




}





